import React from 'react';
import { Grid, Row, Col } from 'react-flexbox-grid';
import { motion } from 'framer-motion';
import { pageTransition, pageVariants} from '../helpers/pageTransition';
import Card from '../components/Card';
import projects from '../projects.json';

class Projects extends React.Component {
  render() {
    return (
      <motion.div initial="out" animate="in" exit="out" variants={pageVariants} transition={pageTransition}>
        <main>
            <Grid>
              <Row center="xs">
                <Col xs={12}>
                  <h1>Projects</h1>
                  <p>Some of my projects i enjoyed working on</p>
                </Col>
              </Row>
              <Row around="xs">
                { 
                  projects.map((project =>
                    <Col>
                      <Card title={project.title} description={project.description} date={project.date} image={project.image} href={project.href} tags={project.tags}/>
                    </Col>
                  ))
                }
              </Row>
            </Grid>
        </main>
      </motion.div>
    )
  }
}
  
  export default Projects;