import React from 'react';
import JSONPretty from 'react-json-pretty';
import { Grid, Row, Col } from 'react-flexbox-grid';
import PackageJson from '../edgus.json';
import { motion } from 'framer-motion';
import { pageTransition, pageVariants} from '../helpers/pageTransition';
import { isMobile } from 'react-device-detect';

var JSONPrettyTheme = {
  main: 'line-height:1.3;color:#f8f8f2;overflow:auto;',
  error: 'line-height:1.3;color:#f8f8f2;overflow:auto;',
  key: 'color:#ff5e5e;',
  string: 'color:#e9fdac;',
  value: 'color:#fdb082;',
  boolean: 'color:#69c;',
}

class Home extends React.Component {
    render() {

      if (isMobile) {
        return(
          <motion.div initial="out" animate="in" exit="out" variants={pageVariants} transition={pageTransition}>
            <main>
                <Grid>
                    <Row>
                        <Col>
                          <div class="intro">
                            <p>Hello, I'm <b>Eddie Gustafsson</b>.</p>
                            <p>I'm a full-stack developer.</p>
                            <a href="/#/contact">
                              <button class="button button--wayra button--border-medium button--text-upper button--size-s button--text-thick button--inverted">Contact me</button>
                            </a>
                          </div>
                        </Col>
                    </Row>
                </Grid>
            </main>
          </motion.div>
        );
      }

      return (
        <motion.div initial="out" animate="in" exit="out" variants={pageVariants} transition={pageTransition}>
          <main>
              <Grid>
                  <Row>
                      <Col xs={8}>
                        <div class="intro">
                          <p>Hello, I'm <b>Eddie Gustafsson</b>.</p>
                          <p>I'm a full-stack developer.</p>
                          <a href="/contact">
                            <button class="button button--wayra button--border-medium button--text-upper button--size-s button--text-thick button--inverted">Contact me</button>
                          </a>
                        </div>
                      </Col>
                      <Col xs={4}>
                        <div class="package">
                          <JSONPretty data={PackageJson} theme={JSONPrettyTheme}></JSONPretty>
                        </div>
                      </Col>
                  </Row>
              </Grid>
          </main>
        </motion.div>
      )
    }
  }
  
  export default Home;